import React from 'react'
import styled from 'styled-components'
import { Outline, Fill } from '..'

export const SButton = styled.button<any>(({ theme, circle, action }) => ({
  position: 'relative',
  display: 'inline-flex',
  alignItems: 'center',
  justifyContent: 'center',
  backgroundColor: 'transparent',
  color: 'inherit',
  height: '2rem',
  width: '7rem',
  margin: 0,
  padding: 0,
  overflow: 'visible !important',
  border: 0,
  cursor: 'pointer',
  borderRadius: '3px',
  transition: theme.transition,

  ...(circle && {
    height: '2rem',
    width: '2rem',
    borderRadius: '100%',
  }),

  ...(theme.isSketched && {}),

  ...(!theme.isSketched && {
    backgroundColor: theme.colors.elementBackground,

    '&:hover': {
      backgroundColor: theme.colors.elementBackgroundHover,
    },

    // ...(action && {
    //   backgroundColor: theme.colors.primary,

    //   '&:hover': {
    //     backgroundColor: theme.colors.primaryLight,
    //   },
    // }),
  }),
}))

export const Button = ({ children, circle }: any) => {
  return (
    <SButton circle={circle}>
      <Fill circle={circle} />
      <Outline radius={circle && 100} shouldAppearInRegularMode />
      {children}
    </SButton>
  )
}
