import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { Code as CodeIcon } from 'react-feather'
import { Row, Col, Title, Spacer } from '3oilerpl4te'
import { Layout, Text, Device, Space } from '../../components'
import { PROJECTS } from '../../constants'

export const ProjectsView = () => {
  const [currentProjectIndex, setCurrentProjectIndex] = useState<number>(0)

  function onNext() {
    if (currentProjectIndex < PROJECTS.length - 1) {
      setCurrentProjectIndex(currentProjectIndex + 1)
    }
  }

  function onPrevious() {
    if (currentProjectIndex > 0) {
      setCurrentProjectIndex(currentProjectIndex - 1)
    }
  }

  function onInfo() {}

  function onExternalLink() {
    window.open(PROJECTS[currentProjectIndex].demo, '_blank')
  }

  return (
    <Layout maxWidth="31rem">
      <Row
        flexDirection={['column', 'row']}
        flexWrap={['wrap', 'nowrap']}
        gutter={60}
      >
        <Col flexGrow={0}>
          <Text>
            <Link to="/">&#x3c; Back</Link>
          </Text>
          <Space />
          <Device
            currentProjectIndex={currentProjectIndex}
            currentProject={PROJECTS[currentProjectIndex]}
            onPrevious={onPrevious}
            onInfo={onInfo}
            onExternalLink={onExternalLink}
            onNext={onNext}
          />
        </Col>
        <Col flexShrink={1} paddingTop="6rem">
          <Text>
            <Title level={4}>{PROJECTS[currentProjectIndex].title}</Title>
            <Space />
            {PROJECTS[currentProjectIndex].description}
            <Space />
            {!PROJECTS[currentProjectIndex].private && (
              <>
                <Spacer direction="row" size={0} alignItems="center">
                  <CodeIcon height="16" width="16" />
                  <a
                    target="_blank"
                    rel="noopener noreferrer"
                    href={PROJECTS[currentProjectIndex].repos.frontend}
                  >
                    Frontend
                  </a>
                </Spacer>
                <Spacer direction="row" size={0} alignItems="center">
                  <CodeIcon height="16" width="16" />
                  <a
                    target="_blank"
                    rel="noopener noreferrer"
                    href={PROJECTS[currentProjectIndex].repos.backend}
                  >
                    Backend
                  </a>
                </Spacer>
              </>
            )}
          </Text>
        </Col>
      </Row>
    </Layout>
  )
}
