import React from 'react'
import { Router, Switch, Route } from 'react-router-dom'
import { createBrowserHistory } from 'history'
import { ThemeProvider } from 'styled-components'
import { GlobalStyle, FormResetStyle } from '3oilerpl4te'
import { HomeView, ProjectsView } from '../views'
import { SApp } from './App.styled'
import { MiscProvider, MiscContext } from '../context'
import { theme, LocalGlobalStyle } from '../style'
import { Lines } from '../components'

export const history = createBrowserHistory()

export const App = () => {
  return (
    <MiscProvider>
      <MiscContext.Consumer>
        {({ isSketched }: any) => (
          <ThemeProvider theme={{ ...theme, isSketched }}>
            <LocalGlobalStyle />
            <FormResetStyle />
            <GlobalStyle />
            <SApp>
              <Lines />
              <Router history={history}>
                <Switch>
                  <Route exact path="/">
                    <HomeView />
                  </Route>
                  <Route path="/projects">
                    <ProjectsView />
                  </Route>
                </Switch>
              </Router>
            </SApp>
          </ThemeProvider>
        )}
      </MiscContext.Consumer>
    </MiscProvider>
  )
}
