import React from 'react'
import styled from 'styled-components'
import { keyGen } from '3oilerpl4te'
import { useHistory } from 'react-router-dom'
import { Button } from '..'
import { SOCIAL } from '../../constants'

export const SSocial = styled.div(({ theme }) => ({
  '> * + *': {
    marginLeft: '1rem',
  },
}))

export const Social = () => {
  const history = useHistory()

  return (
    <SSocial>
      {SOCIAL.map(({ name, icon, url }: any) => (
        <Button key={keyGen()} circle onClick={() => history.push(url)}>
          {icon}
        </Button>
      ))}
    </SSocial>
  )
}
