import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { times } from 'lodash'
import { rgba, keyGen } from '3oilerpl4te'
import {
  ChevronLeft as PrevIcon,
  Info as InfoIcon,
  ExternalLink as ExternalLinkIcon,
  ChevronRight as NextIcon,
} from 'react-feather'
import { Fill, Outline } from '..'

const SDeviceWrapper = styled.div<any>(() => ({
  perspective: '1500px',
}))

const SDevice = styled.div<any>(({ width, height, currentProjectIndex }) => ({
  position: 'relative',
  flexGrow: 1,
  transformStyle: 'preserve-3d',
  width: `${width}rem`,
  height: `${height}rem`,
  transform: `rotateY(${360 * currentProjectIndex}deg)`,
  transition: 'all 1s ease',
}))

const SDeviceScreen = styled.div<any>(({ theme }) => ({
  position: 'relative',
  display: 'flex',
  flexDirection: 'column',
  width: '100%',
  height: '100%',
  borderRadius: '0.5rem',

  ...(!theme.isSketched && {
    border: `1px solid ${rgba('white', 0.6)}`,
    backgroundColor: rgba('black', 0.9),
  }),
}))

const SDeviceScreenContent = styled.div<any>(({ theme }) => ({
  position: 'relative',
  display: 'flex',
  flexGrow: 1,
  overflow: 'hidden',
  borderRadius: '0.5rem 0.5rem 0 0',

  iframe: {
    width: '100%',
    height: '100%',

    ...(theme.isSketched && {
      display: 'none',
    }),
  },
}))

const SDeviceScreenButtons = styled.div<any>(({ theme }) => ({
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-around',
  padding: '0.5rem',
  height: '2rem',
  width: '100%',
}))

const SDeviceFace = styled.div<any>(
  ({ theme, type, width, height, depth, radius }) => ({
    position: 'absolute',
    backgroundColor: rgba(theme.colors.primary, 0.3),
    border: `1px solid ${rgba('white', 0.3)}`,

    ...(theme.isSketched && {
      backgroundColor: 'transparent',
    }),

    ...(type === 'front' && {
      width: `${width}rem`,
      height: `${height}rem`,
      borderRadius: `${radius}rem`,
      transform: `translateZ(${depth / 2}rem)`,
      padding: '2rem 1rem',
    }),

    ...(!theme.isSketched && {
      ...(type === 'back' && {
        width: `${width}rem`,
        height: `${height}rem`,
        borderRadius: `${radius}rem`,
        transform: `rotateY(180deg) translateZ(${depth / 2}rem)`,
      }),

      ...(type === 'top' && {
        top: 0,
        height: `${depth}rem`,
        width: `${width - radius * 2}rem`,
        marginLeft: `${radius}rem`,
        transform: `rotateX(90deg) translateZ(${depth / 2}rem)`,
      }),

      ...(type === 'bottom' && {
        bottom: 0,
        height: `${depth}rem`,
        width: `${width - radius * 2}rem`,
        marginLeft: `${radius}rem`,
        transform: `rotateX(-90deg) translateZ(${depth / 2}rem)`,
      }),

      ...(type === 'left' && {
        left: 0,
        width: `${depth}rem`,
        height: `${height - radius * 2}rem`,
        marginTop: `${radius}rem`,
        transform: `rotateY(-90deg) translateZ(${depth / 2}rem)`,
      }),

      ...(type === 'right' && {
        right: 0,
        width: `${depth}rem`,
        height: `${height - radius * 2}rem`,
        marginTop: `${radius}rem`,
        transform: `rotateY(90deg) translateZ(${depth / 2}rem)`,
      }),
    }),
  }),
)

const SDeviceCorner = styled.div<any>(
  ({ theme, type, width, radius, depth }) => ({
    position: 'absolute',
    transformStyle: 'preserve-3d',
    transform: `rotateX(90deg) translateY(-${depth / 2}rem)`,

    ...(type === 'topRight' && {
      top: `${radius}rem`,
      right: `${radius + width / 2}rem`,
    }),

    ...(type === 'topLeft' && {
      top: `${radius}rem`,
      left: `${radius - width / 2}rem`,
    }),

    ...(type === 'bottomLeft' && {
      bottom: `${radius}rem`,
      left: `${radius - width / 2}rem`,
    }),

    ...(type === 'bottomRight' && {
      bottom: `${radius}rem`,
      right: `${radius + width / 2}rem`,
    }),

    ...(theme.isSketched && {
      display: 'none',
    }),
  }),
)

const SDeviceCornerPiece = styled.div<any>(
  ({ theme, width, height, x, z, rotate }) => ({
    position: 'absolute',
    width: `${width}rem`,
    height: `${height}rem`,
    transform: `translateX(${x}rem) translateZ(${z}rem) rotateY(${rotate}deg)`,
    backgroundColor: rgba(theme.colors.primary, 0.3),

    ...(theme.isSketched && {
      display: 'none',
    }),
  }),
)

export const Device = ({
  currentProject,
  currentProjectIndex,
  onPrevious,
  onInfo,
  onExternalLink,
  onNext,
}: any) => {
  const deviceDimensions: any = {
    width: 12,
    height: 22,
    depth: 1.5,
    radius: 1,
  }
  const faces: string[] = ['front', 'back', 'top', 'left', 'right', 'bottom']
  const corners: string[] = ['topRight', 'topLeft', 'bottomLeft', 'bottomRight']

  const angle = 360
  const cornerPieceAmount = 16

  const [cornerPieces, setCornerPieces]: any = useState<any>([])

  function toDegrees(angle: number) {
    return (angle / (180 * Math.PI)) * 10
  }

  const cornerWidth =
    2 *
    deviceDimensions.radius *
    Math.sin(toDegrees(angle / cornerPieceAmount / 2))

  function calcCornerPieces(height: number) {
    const a = angle / cornerPieceAmount
    const k = deviceDimensions.radius * Math.cos(toDegrees(a / 2))
    const w = 2 * deviceDimensions.radius * Math.sin(toDegrees(a / 2))

    const pieces: any = []

    times(cornerPieceAmount, (i: number) => {
      const b = a * i + a / 2
      const x = k * Math.cos(toDegrees(b))
      const z = k * Math.sin(toDegrees(b))
      const rotate = 90 - b
      pieces.push({ width: w, height, x, z, rotate })
    })

    setCornerPieces(pieces)
  }

  useEffect(() => {
    calcCornerPieces(deviceDimensions.depth)
  }, [])

  return (
    <SDeviceWrapper>
      <SDevice
        width={deviceDimensions.width}
        height={deviceDimensions.height}
        currentProjectIndex={currentProjectIndex}
      >
        {faces.map((type: string) => (
          <SDeviceFace
            key={keyGen()}
            type={type}
            width={deviceDimensions.width}
            height={deviceDimensions.height}
            depth={deviceDimensions.depth}
            radius={deviceDimensions.radius}
          >
            {type === 'front' && (
              <>
                <Outline radius="1rem" />
                <SDeviceScreen>
                  <Outline radius="0.5rem" />
                  <SDeviceScreenContent>
                    <Outline />
                    <Fill />
                    <iframe
                      src={currentProject.demo}
                      title={currentProject.name}
                      frameBorder="0"
                    />
                  </SDeviceScreenContent>
                  <SDeviceScreenButtons>
                    <button type="button" onClick={onPrevious}>
                      <PrevIcon width={20} />
                    </button>
                    <button type="button" onClick={onInfo}>
                      <InfoIcon width={16} />
                    </button>
                    <button type="button" onClick={onExternalLink}>
                      <ExternalLinkIcon width={16} />
                    </button>
                    <button type="button" onClick={onNext}>
                      <NextIcon width={20} />
                    </button>
                  </SDeviceScreenButtons>
                </SDeviceScreen>
              </>
            )}
          </SDeviceFace>
        ))}
        {corners.map((type: string, cornerIndex: number) => (
          <SDeviceCorner
            key={keyGen()}
            type={type}
            width={cornerWidth}
            radius={deviceDimensions.radius}
            depth={deviceDimensions.depth}
          >
            {times(4, (cornerPieceIndex: number) => {
              return (
                <SDeviceCornerPiece
                  key={keyGen()}
                  {...cornerPieces[
                    corners.length * cornerIndex + cornerPieceIndex
                  ]}
                />
              )
            })}
          </SDeviceCorner>
        ))}
      </SDevice>
    </SDeviceWrapper>
  )
}
