import React, { useState, useEffect } from 'react'
import { times, debounce } from 'lodash'
import { rgba } from '3oilerpl4te'
import styled, { keyframes, css } from 'styled-components'
import { getContainerWidth } from '../../../helpers'

const SLinesWrapper = styled.div<any>(() => ({
  position: 'absolute',
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  pointerEvents: 'none',

  svg: {
    overflow: 'visible',
  },
}))

const SLines = styled.svg<any>(({ isVertical }) => ({
  position: 'absolute',
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,

  ...(isVertical && {
    marginLeft: 'auto',
    marginRight: 'auto',
  }),
}))

const regularEvenLinesX = ({ strokeDashoffset }: any) => keyframes`
  from { stroke-dashoffset: 0; }
  to { stroke-dashoffset: -${strokeDashoffset} }
`

const regularOddLinesX = ({ strokeDashoffset }: any) => keyframes`
  from { stroke-dashoffset: 0; }
  to { stroke-dashoffset: ${strokeDashoffset} }
`

const sketchLinesX = ({ strokeDashoffset }: any) => keyframes`
  from { stroke-dashoffset: ${strokeDashoffset} }
  to { stroke-dashoffset: 0; }
`

const regularEvenLinesY = ({ strokeDashoffset }: any) => keyframes`
  from { stroke-dashoffset: 0; }
  to { stroke-dashoffset: -${strokeDashoffset} }
`

const regularOddLinesY = ({ strokeDashoffset }: any) => keyframes`
  from { stroke-dashoffset: 0; }
  to { stroke-dashoffset: ${strokeDashoffset} }
`

const sketchLinesY = ({ strokeDashoffset }: any) => keyframes`
  from { stroke-dashoffset: ${strokeDashoffset} }
  to { stroke-dashoffset: 0; }
`

const SLinesLine = styled.line<any>(
  ({ theme, isVertical }) => ({
    stroke: theme.colors.sketch.paperLines,
    strokeWidth: 1,
    shapeRendering: 'crispEdges',

    ...(!theme.isSketched && {
      stroke: rgba(theme.colors.primary, 0.5),
      strokeDashoffset: 0,
    }),

    ...(isVertical && {
      marginLeft: 'auto',
      marginRight: 'auto',
    }),
  }),
  ({
    theme,
    isEven,
    isOdd,
    isHorizontal,
    isVertical,
    duration,
    delay,
    strokeDashoffset,
  }) => {
    if (theme.isSketched) {
      if (isVertical) {
        return css`
          stroke-dashoffset: ${strokeDashoffset};
          animation: ${sketchLinesY({ strokeDashoffset })} ${duration || '1s'}
            ease-out ${delay || '0.5s'} forwards;
        `
      }
      if (isHorizontal) {
        return css`
          stroke-dashoffset: ${strokeDashoffset};
          animation: ${sketchLinesX({ strokeDashoffset })} ${duration || '1s'}
            ease-out ${delay || '0.5s'} forwards;
        `
      }
    }

    if (!theme.isSketched) {
      if (isVertical && isEven) {
        return css`
          animation: ${regularEvenLinesY({ strokeDashoffset })}
            ${duration || '1s'} ease-out ${delay || '0.5s'} forwards;
        `
      }
      if (isVertical && isOdd) {
        return css`
          animation: ${regularOddLinesY({ strokeDashoffset })}
            ${duration || '1s'} ease-out ${delay || '0.5s'} forwards;
        `
      }
      if (isHorizontal && isEven) {
        return css`
          animation: ${regularEvenLinesX({ strokeDashoffset })}
            ${duration || '1s'} ease-out ${delay || '0.5s'} forwards;
        `
      }
      if (isHorizontal && isOdd) {
        return css`
          animation: ${regularOddLinesX({ strokeDashoffset })}
            ${duration || '1s'} ease-out ${delay || '0.5s'} forwards;
        `
      }
    }

    return null
  },
)

export const Lines = ({ duration, delay }: any) => {
  const [values, setValues] = useState<any>(null)

  function updateValues() {
    setValues({
      maxWidth: document.body.getBoundingClientRect().width,
      maxHeight: document.body.getBoundingClientRect().height,
      blockSize: parseInt(
        window
          .getComputedStyle(document.documentElement)
          .getPropertyValue('font-size'),
        10,
      ),
      containerWidth: getContainerWidth(),
    })
  }

  useEffect(() => {
    updateValues()
    window.addEventListener('resize', debounce(updateValues, 50))

    return () => {
      window.removeEventListener('resize', debounce(updateValues, 50))
    }
  }, [])

  return values ? (
    <SLinesWrapper>
      <SLines
        width={values.maxWidth}
        height={values.maxHeight}
        viewBox={`0 0 ${values.maxWidth} ${values.maxHeight}`}
      >
        {times(
          Math.floor(values.maxHeight / values.blockSize) + 2,
          (index: number) => (
            <SLinesLine
              key={index - 1}
              x1={values.maxWidth}
              y1={(index - 1) * values.blockSize}
              y2={(index - 1) * values.blockSize}
              strokeDasharray={values.maxWidth}
              strokeDashoffset={values.maxWidth}
              isHorizontal
              isEven={index % 2 === 0}
              isOdd={index % 2 !== 0}
              duration={duration}
              delay={delay}
            />
          ),
        )}
      </SLines>
      <SLines
        width={values.containerWidth}
        height={values.maxHeight}
        viewBox={`0 0 ${values.containerWidth} ${values.maxHeight}`}
        isVertical
      >
        {times(
          Math.floor(values.maxWidth / values.blockSize) + 1,
          (index: number) => (
            <SLinesLine
              key={index}
              y1={values.maxHeight}
              x1={index * values.blockSize}
              x2={index * values.blockSize}
              strokeDasharray={values.maxHeight}
              strokeDashoffset={values.maxHeight}
              isEven={index % 2 === 0}
              isOdd={index % 2 !== 0}
              isVertical
            />
          ),
        )}
      </SLines>
    </SLinesWrapper>
  ) : null
}
